.modelo1 {
  width: 95%;
  overflow-wrap: break-word;
  margin-top: 70px;
  display: flex;
  flex-direction: column;
}
.modelo1 h1 {
  color: white;
  font-family: montserrat;
  font-weight: bold;
  text-align: center;
  font-size: 23px;
}
.modelo1 h2 {
  color: white;
  background-color: red;
  font-family: montserrat;
  font-weight: bold;
  font-size: 23px;
  text-align: center;
  padding: 0px 5px;
}
.modelo2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 70px;
  text-align: center;
  width: 95%;
}

.modelo2 span {
  color: white;
  font-size: 15px;
  font-family: montserrat;
  font-weight: bold;
}
.modelo2 svg {
  color: red;
  margin-bottom: -2px;
  position: relative;
  right: 2px;
}

.modelo3 {
  width: 95%;
  margin-top: 30px;
  position: relative;
}
.modelo3 video {
  width: 100%;
}

.modelo4 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  text-align: center;
  width: 95%;
  margin-bottom: 50px;
}

.modelo4 span {
  color: white;
  font-size: 15px;
  font-family: montserrat;
  font-weight: bold;
}
.modelo4 svg {
  color: red;
  margin-bottom: -2px;
  position: relative;
  right: 2px;
}

.modelo5 button {
  background-color: red;
  border: 0;
  display: flex;
  color: white;
  font-family: montserrat;
  border-radius: 30px;
  padding: 10px;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  width: 70%;
  cursor: pointer;
  font-weight: 600;
  animation: pulse 1.5s infinite;
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.modelo5 {
  margin-bottom: 70px;
}
.modelo5 a {
  text-decoration: none;
}

.pause_overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.pause_icon {
  width: 48px;
  height: 48px;
  fill: #ffffff;
}
@media screen and (min-width: 992px) and (max-width: 4000px) {
  .modelo3 video {
    height: 400px;
  }
}
